.button {
    border-radius: 25px;
    background-color: #479ff8;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 18px;
    padding:6px;
    width: 215px;
    transition: all 0.5s;
    cursor: pointer;
    margin-left: 5%;
}
  
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }
  @media (min-width: 1200px) {
    .container{
        max-width: 1300px;
    }
}

